@import url('https://fonts.googleapis.com/css?family=Work+Sans');
.amount
{
    bottom:100px;
    position: fixed;
    width:110%;
    padding: 2%;
    overflow: hidden;
    margin-left: -2%;
}
.primary-text
{
    color:green;
}
.secondary-text
{
    color:red;
}
.alert
{
    color:black;
    font-size: 20px;
    
    text-align: center;
    font-family:'Dancing Script', sans-serif;
}
.space
{
    padding: 20px;
}
@media(max-width:700px)
{
    .amount
    {
        bottom:80px;
    }
    .alert
{
    color:black;
    font-size: 13px;
}
} 


