@import url('https://fonts.googleapis.com/css?family=Work+Sans');

.navigation {
    text-align: center;
    margin-top: 0px;
    background-color: #0b7696a9;
    left: 0em;
    padding: .5em;
    width:100%;
    font-family: 'Dancing Script', sans-serif;
}
@media(max-width:700px)
{
    .navigation
    {
        padding: 0em;
    }
    .navbar-brand
    {
        padding: 0;
    }
}


