.displaydetails
{
    margin-top: 100px;
}
.displaydetails .row
{
    line-height: 30px;
    -webkit-animation-duration: 2s;
            animation-duration: 2s;
   
    /* The element to apply the animation to */

      -webkit-animation-name: example;

              animation-name: example;
      -webkit-animation-duration: 1s;
              animation-duration: 1s;
}
@-webkit-keyframes example {
  from {opacity:0;}
  to {opacity: 1;}
}
@keyframes example {
  from {opacity:0;}
  to {opacity: 1;}
}

.sno{
    border: 0;
    outline: 0;
    background: transparent;
    border-bottom: 1px solid black;
    margin-right: 10px;
    width:10%;
    color: #0f8392f6;
    text-align: center;
  }
  .del{
    border: 0;
    outline: 0;
    background: transparent;
    margin-right: 10px;
    margin-top: 10px;
    width:5%;
    color: #0b7696a9;
    text-align: center;
  }

  .expensetitle{
    border: 0;
    outline: 0;
    background: transparent;
    border-bottom: 1px solid black;
    margin-right:10px;
    width:40%;
    color: #0b7696a9;
    text-align: center
  }
  .expenserecord{
    border: 0;
    outline: 0;
    background: transparent;
    border-bottom: 1px solid black;
    margin-right:10px;
    width:40%;
    color: #333;
    text-align: center;
    overflow: auto;
  }
  .costtitle{
    border: 0;
    outline: 0;
    background: transparent;
    border-bottom: 1px solid black;
    margin-right: 5px;
    width:20%;
    color: #0b7696a9;
    text-align: center
  }
  .costrecord{
    border: 0;
    outline: 0;
    background: transparent;
    border-bottom: 1px solid black;
    margin-right: 5px;
    width:20%;
    color: #333;
    text-align: center
  }
  .close {
    position: absolute;
    right: 0;
    padding: 12px 16px 12px 16px;
  }
  .displaydetails 
  {
    height: 325px;
    overflow: scroll;
  }
 
    .title3 {
        text-align: center;
        font-family:'Dancing Script', sans-serif;
      }
@media(max-width:700px)
{
  .displaydetails .row
  {
      line-height: 20px;
  }
  .expenserecord, .expensetitle, .costtitle, .costrecord,.sno,.del
  {
    font-size: 12px;
  }
  .title3 {
    font-size: 20px;
  }
 
  .displaydetails
{
    margin-top:55px;
}
} 


