@import url('https://fonts.googleapis.com/css?family=Work+Sans');

.Expenseform {
    text-align: center;
    padding: .5em;
    margin-bottom: 0;
    width:100%;
    left:0;
    right:0;
}
.title2 {
    text-align: center;
    font-family:'Dancing Script', sans-serif;
  }
  .row
  {
      -ms-flex-pack: center;
          justify-content: center;
      padding:0 5%;
  }
  input[type="text"] {
    border: 0;
    outline: 0;
    background: transparent;
    border-bottom: 2px solid #333;
    margin-right: 20px;
    width:50%;
    color:black;
  }
  ::-webkit-input-placeholder
  {
      color:#333;
      padding:0 10px;
  }
  :-ms-input-placeholder
  {
      color:#333;
      padding:0 10px;
  }
  ::-ms-input-placeholder
  {
      color:#333;
      padding:0 10px;
  }
  ::placeholder
  {
      color:#333;
      padding:0 10px;
  }
  input[type="number"] {
    border: 0;
    outline: 0;
    background: transparent;
    border-bottom: 2px solid #333;
    margin-right: 20px;
    width:30%;
    color:black;
    }
    button{
        padding:3px 25px;
        background: #0b7696a9;
        color:#333;  
        border:1px solid black;
        border-radius: 3px;  
    }
  @media(max-width:700px)
  {
      .row{
          padding: 0 0%;
      }
      input[type="number"] {
          width:20%;
      }
      input[type="text"] {
        width:35%;
    }
      button{
        margin-top: 0%;
        padding: 1px 15px;
        font-size: 12px;
      }
      .form{
          padding:.25em;
          padding-top: 0px;
      }
      .title2
      {
          font-size: 20px;
          margin: 0;
          padding: .5em;
      }
      input{
        font-size: 12px;
      }
  }


